.homepage-hero {
  position: relative;
  color: var(--text-on-surface-primary-darker);
  background-color: var(--surface-primary-dark);
}

.homepage-hero__image-wide {
  display: none;
}
@media (width >= 1200px) {
  .homepage-hero__image-wide {
    display: contents;
  }
}
.homepage-hero__image-wide img {
  position: absolute;
  left: 0;
  top: 0;
  width: 60%;
  height: 100%;
  object-fit: cover;
}

.homepage-hero__image-narrow {
  display: contents;
}
@media (width >= 1200px) {
  .homepage-hero__image-narrow {
    display: none;
  }
}
.homepage-hero__image-narrow img {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
}

@media (width >= 1200px) {
  .homepage-hero__container {
    display: flex;
    width: 100%;
    margin: 0 auto;
  }
}

.homepage-hero__left {
  position: relative;
  height: 500px;
  margin-inline: calc(-1 * var(--container-inline-spacing));
}
@media (width >= 1200px) {
  .homepage-hero__left {
    width: 60%;
    height: auto;
    margin-inline: unset;
  }
}

.homepage-hero__right {
  position: relative;
  margin-inline: calc(-1 * var(--container-inline-spacing));
  padding-block: 32px;
  padding-inline: var(--container-inline-spacing);
  background-color: var(--surface-primary-dark);
}
@media (width >= 1200px) {
  .homepage-hero__right {
    width: 40%;
    margin-inline: 0;
    padding-block: 40px;
    padding-inline: 64px 0;
  }
}
.homepage-hero__right .paragraph--type--hero-cta-block {
  padding-bottom: 24px;
}
@media (width >= 700px) {
  .homepage-hero__right .paragraph--type--hero-cta-block {
    padding-bottom: 32px;
  }
}
.homepage-hero__right .paragraph--type--hero-cta-block:last-child {
  padding-bottom: 0;
}
.homepage-hero__right .hero-block-secondary {
  max-width: 650px;
  margin-inline: auto;
}

.homepage-hero__information {
  position: absolute;
  bottom: 0;
  color: var(--text-on-surface-primary-lighter);
  padding: 24px var(--container-inline-spacing);
  width: 100%;
}
@media (width >= 700px) {
  .homepage-hero__information {
    padding: 32px 64px;
  }
}
@media (width >= 1200px) {
  .homepage-hero__information {
    padding-block: 24px;
    padding-inline: 0 64px;
  }
}
.homepage-hero__information:before {
  position: absolute;
  top: 0;
  right: 0;
  width: 100%;
  height: 100%;
  border-top: 5px solid var(--accent);
  content: "";
  background-color: rgba(228, 228, 228, 0.9);
}
@media (width >= 1200px) {
  .homepage-hero__information:before {
    --wide-width: calc(100% + (50vw + var(--container-inline-spacing) - var(--site-max-width)/2));
    --medium-width: calc(100% + var(--container-inline-spacing));
    width: max(var(--medium-width), var(--wide-width));
  }
}

.homepage-hero__title {
  position: relative;
  margin: 0;
  font-size: 28px;
  line-height: 33.6px;
  text-wrap: pretty;
}
@media (width >= 1200px) {
  .homepage-hero__title {
    font-size: 40px;
    line-height: 1.15;
  }
}

.homepage-hero__description {
  position: relative;
  margin: 0;
  line-height: 1.5;
  font-size: 17px;
  font-weight: 400;
  text-align: left;
}
.homepage-hero__description :first-child {
  margin-top: 0;
}
.homepage-hero__description :last-child {
  margin-bottom: 0;
}

.homepage-hero__cta-wrapper {
  position: relative;
  margin-top: 16px;
}
.homepage-hero__cta-wrapper a:before {
  display: block;
  width: 18px;
  height: 18px;
  content: "";
  background-color: currentColor;
  mask-image: url("images/court.svg");
  mask-size: contain;
  mask-repeat: no-repeat;
  mask-position: center;
}
@media (forced-colors: active) {
  .homepage-hero__cta-wrapper a:before {
    background-color: buttonText;
  }
}

@media (width >= 700px) {
  .quicklinks-wrapper--hero-default[class] {
    padding-inline: 48px;
  }
}
@media (width >= 1200px) {
  .quicklinks-wrapper--hero-default[class] {
    padding-inline: var(--container-inline-spacing);
  }
}
.quicklinks-wrapper--hero-default[class] .quicklinks {
  margin-top: 32px;
}
.quicklinks-wrapper--hero-default[class] .quicklinks__title {
  margin: 0;
}
.quicklinks-wrapper--hero-default[class] .quicklinks__list {
  gap: 20px 24px;
  margin-top: 8px;
  padding-block: 16px;
}